import React, { useState } from "react";
import { Pagination, Segment, Button } from "semantic-ui-react";

import "./Slides.css";

export default function Slides({ editor }) {
  const [page, setPage] = useState(0);
  const content = editor.querySelector(".ProseMirror").cloneNode(true);

  let currentSlide = [];
  const slides = [currentSlide];
  Array.from(content.children).forEach((node, i) => {
    if (i === 0) {
      currentSlide.push(node);
      return;
    }

    if (node.classList.contains("heading")) {
      currentSlide = [node];
      slides.push(currentSlide);
      return;
    }

    currentSlide.push(node);
  });

  return (
    <div className="Editor--slides">
      <Pagination
        defaultActivePage={1}
        totalPages={slides.length}
        onPageChange={(_, { activePage }) => {
          setPage(activePage - 1);
        }}
      />
      <Segment>
        <div
          dangerouslySetInnerHTML={{
            __html: slides[page].map(node => node.outerHTML).join("")
          }}
        />
      </Segment>

      <Button
        content="Download"
        positive
        icon="down arrow"
        labelPosition="left"
        floated="right"
      />
      <Button
        content="Preview"
        icon="play"
        labelPosition="left"
        floated="right"
      />
      <div style={{ clear: "both" }} />
    </div>
  );
}
