import React, { Component } from "react";
import { Sidebar, Segment, Icon } from "semantic-ui-react";
import PropTypes from "prop-types";
import { Menu } from "semantic-ui-react";

import SideMenu from "./Menu/Menu";
import Editor from "./Editor/Editor.js";

import "./App.css";

class App extends Component {
  static propTypes = {
    firebase: PropTypes.object.isRequired
  };

  state = { visible: true };

  render() {
    const { visible } = this.state;
    const { firebase } = this.props;
    return (
      <>
        {visible === false && (
          <Menu className="Menu--Trigger" compact icon size="tiny">
            <Menu.Item
              name="caret square left"
              onClick={() => {
                document.querySelector("#pushable").scrollTo({
                  top: 0,
                  behavior: "smooth"
                });
                this.setState({ visible: !visible });
              }}
            >
              <Icon name="caret square right" />
            </Menu.Item>
          </Menu>
        )}
        <Sidebar.Pushable id="pushable">
          <SideMenu
            visible={visible}
            onToggle={() => this.setState({ visible: !visible })}
            firebase={firebase}
          />

          <Sidebar.Pusher dimmed={visible}>
            <Editor />
          </Sidebar.Pusher>
        </Sidebar.Pushable>
      </>
    );
  }
}

export default App;
