import React, { createRef } from "react";
import { EditorView } from "prosemirror-view";
import { EditorState } from "prosemirror-state";
import { Icon, Menu, Segment, Ref, Sticky, Dropdown } from "semantic-ui-react";

import Stats from "../Stats/Stats";
import Slides from "../Slides/Slides";
import Agenda from "../Agenda/Agenda";
import CheatSheet from "../CheatSheet/CheatSheet";
import CommandsMenu from "./CommandsMenu";

import { showError } from "../errors";
import { getStorage } from "../Storage";
import { schema, plugins } from "./setup";
import { decorationsHandlers } from "./decorations";
import { parseTodoNode, getTextContent } from "../util";

import "./Editor.css";

// This is used as part of the Prose element key. If you
// increment it and re-render <Editor>, Prose will remount.
let __remountKey = 0;

const EDITOR_SHOWN = "EDITOR_SHOWN";
const HELP_SHOWN = "HELP_SHOWN";
const AGENDA_SHOWN = "AGENDA_SHOWN";
const STATS_SHOWN = "STATS_SHOWN";
const SLIDES_SHOWN = "SLIDES_SHOWN";

class Editor extends React.Component {
  _componentElementRef = createRef();
  _stickyMenuContextRef = createRef();

  constructor(props) {
    super(props);

    const store = getStorage();

    this._editorNode = null;
    this._editorView = null;
    this._editorState = null;

    this.state = {
      shown: EDITOR_SHOWN,
      uploading: false,
      note: store.getSelectedNote()
    };

    this.rerender = this.forceUpdate.bind(this);
    this.storeUnsubscribe = store.subscribe(meta => {
      const selected = store.getSelectedNote();

      // Server updates should remount Prose in order to show
      // updated state.
      if (meta && meta.isServer) {
        console.count("EDITOR_REMOUNT"); // TODO: Prevent initial remount.

        __remountKey++;
        this.setState({ note: selected });
        return;
      }

      if (this.state.note.id !== selected.id) {
        this.setState({
          note: selected,
          shown: EDITOR_SHOWN
        });
      }
    });
  }

  componentDidMount() {
    const elm = this._componentElementRef.current;
    elm.addEventListener("drop", this.handleDrop, false);
    elm.addEventListener("click", this.handleClick, false);
    elm.addEventListener("keyup", this.handleCTRLUp, false);
    elm.addEventListener("keydown", this.handleCTRLDown, false);
  }

  componentWillUnmount() {
    if (this._editorView) {
      this._editorView.destroy();
    }

    const elm = this._componentElementRef.current;
    elm.removeEventListener("drop", this.handleDrop);
    elm.removeEventListener("click", this.handleClick);
    elm.removeEventListener("keyup", this.handleCTRLUp);
    elm.removeEventListener("keydown", this.handleCTRLDown);

    this.storeUnsubscribe();
  }

  _createEditorView = element => {
    if (element != null) {
      this._editorNode = element;
      const store = getStorage();
      const { note } = this.state;
      const content = store.getSelectedNoteContent();

      this._editorState =
        // NOTE: If content is an empty Object just create a new one.
        content && Object.keys(content).length > 0
          ? EditorState.fromJSON(
              {
                schema,
                plugins
              },
              content
            )
          : EditorState.create({
              schema,
              plugins
            });

      this._editorView = new EditorView(element, {
        state: this._editorState,
        dispatchTransaction: tx => {
          this._editorState = this._editorState.apply(tx);
          this._editorView.updateState(this._editorState);

          if (!tx.docChanged) return;

          store.updateNote(
            this.state.note.id,
            // TODO: Delay these to the actual
            // saving to improve performance.
            {
              meta: {
                title: this.getTitle(),
                tags: this.getTags(),
                todos: this.getTodos().map(t => t.toJSON()),
                images: this.getImages(),
                summary: this.getSummary(),
                highlights: this.getHighlights()
              }
            },
            this._editorState.toJSON()
          );
        }
      });

      this.onFocus();
    }
  };

  // ===========================================================================
  // Handlers

  handleCTRLDown = e => {
    // Prevent default tab behavior.
    if (e.keyCode === 9) {
      e.preventDefault();
      return;
    }

    // Allow clicking links when pressing control.
    if (e.keyCode === 91) {
      [...document.querySelectorAll("a")].forEach(
        a => (a.contentEditable = false)
      );
    }
  };

  handleCTRLUp = e => {
    if (e.keyCode === 91) {
      [...document.querySelectorAll("a")].forEach(
        a => (a.contentEditable = true)
      );
    }
  };

  handleClick = e => {
    Object.keys(decorationsHandlers).forEach(className => {
      if (e.target.classList.contains(className)) {
        decorationsHandlers[className](e, e.target, this._editorView);
      }
    });
  };

  handleDrop = e => {
    if (
      !(
        e.dataTransfer &&
        e.dataTransfer.files &&
        e.dataTransfer.files.length > 0
      )
    )
      return;

    const images = Array.from(e.dataTransfer.files).filter(file =>
      /image/i.test(file.type)
    );
    if (images.length === 0) {
      // TODO: Show error that only images are supported.
      // TODO: Show error on wrong filessize;
      return;
    }

    e.preventDefault();
    this.setState({ uploading: true });

    getStorage()
      .uploadFile(images[0])
      .then(url => {
        this._editorView.dispatch(
          this._editorView.state.tr.insertText(` [${url}] `).scrollIntoView()
        );
        this.setState({ uploading: false });
      })
      .catch(err => {
        showError(err);
        this.setState({ uploading: false });
      });
  };

  // ===========================================================================
  // Commands

  getTitle() {
    const heading =
      this._editorNode && this._editorNode.querySelector(".heading");
    return heading
      ? heading.lastChild.textContent.trim().replace(/#+/g, "")
      : "--";
  }

  getTodos() {
    return (
      this._editorNode &&
      [...this._editorNode.querySelectorAll(".todo")]
        .map(n => n.parentNode)
        .map(n => parseTodoNode(n))
    );
  }

  getImages() {
    return (
      this._editorNode &&
      [...this._editorNode.querySelectorAll(".img-text")].map(n =>
        n.textContent.trim().slice(1, -1)
      )
    );
  }

  getTags() {
    const tags = this._editorNode && this._editorNode.querySelectorAll(".tag");
    return tags ? [...tags].map(t => t.textContent.slice(1)) : [];
  }

  getSummary() {
    return (
      this._editorNode &&
      Array.from(
        this._editorNode.querySelectorAll(
          ".ProseMirror > p:not(.ProseMirror-widget):nth-of-type(-n+5)"
        )
      )
        .map(p => p.innerText)
        .join(" ")
        .replace(/\.\.\./g, "")
        .slice(0, 100)
    );
  }

  getHighlights() {
    return (
      this._editorNode &&
      Array.from(this._editorNode.querySelectorAll(".highlight")).map(p =>
        p.innerText.slice(1, -1)
      )
    );
  }

  getTextContent = () => {
    const editor = this._editorNode
      .querySelector(".ProseMirror")
      .cloneNode(true);

    editor
      .querySelectorAll(".ProseMirror-widget")
      .forEach(w => w.parentNode.removeChild(w));

    return Array.from(editor.childNodes)
      .map((n, i) =>
        /H\d/i.test(n.tagName) && !!i ? `\n${n.textContent}\n` : n.textContent
      )
      .join("\n")
      .replace(new RegExp(" ", "g"), " ");
  };

  getHeadings() {
    const headings =
      this._editorNode &&
      Array.from(this._editorNode.querySelectorAll(".heading"));

    if (headings.length === 0) return [];

    const result = headings.map(elm => {
      const t = getTextContent(elm);
      const level = t.match(/#*/)[0].length;
      const text = t.replace(/#*\s*/, "");
      return { level, text, elm };
    });

    return result;
  }

  // ===========================================================================
  // Handlers

  toggleMenuItem = item => {
    this._editorNode.parentNode.scrollTo({
      top: 0,
      behavior: "smooth"
    });
    this.setState({ shown: item });
  };
  showEditor = () => this.toggleMenuItem(EDITOR_SHOWN);
  toggleHelp = () => this.toggleMenuItem(HELP_SHOWN);
  toggleStats = () => this.toggleMenuItem(STATS_SHOWN);
  toggleSlides = () => this.toggleMenuItem(SLIDES_SHOWN);
  toggleAgenda = () => this.toggleMenuItem(AGENDA_SHOWN);

  // TODO: Example of commands for menu or something
  onFocus() {
    if (this._editorView) {
      this._editorView.focus();
    }
  }

  // ===========================================================================
  // Rendering

  renderHelp() {
    return (
      <div className="Editor--help">
        <CheatSheet />
      </div>
    );
  }

  renderMenu() {
    const { shown } = this.state;
    return (
      <Sticky context={this._stickyMenuContextRef}>
        <div className="Editor--menu">
          <Menu icon="labeled" stackable>
            <Menu.Item
              name="file alternate outline"
              active={shown === EDITOR_SHOWN}
              onClick={this.showEditor}
              id="prose-show-editor"
            >
              <Icon name="file alternate outline" />
              Editor
            </Menu.Item>
            <Menu.Item
              name="clipboard list"
              active={shown === AGENDA_SHOWN}
              onClick={this.toggleAgenda}
              id="prose-show-agenda"
            >
              <Icon name="clipboard list" />
              Agenda
            </Menu.Item>

            <Menu.Item
              name="tv"
              active={shown === SLIDES_SHOWN}
              onClick={this.toggleSlides}
              id="prose-show-slides"
            >
              <Icon name="tv" />
              Slides
            </Menu.Item>

            <Menu.Item
              name="chart line"
              active={shown === STATS_SHOWN}
              onClick={this.toggleStats}
              id="prose-show-stats"
            >
              <Icon name="chart line" />
              Stats
            </Menu.Item>

            <Menu.Item
              name="help"
              active={shown === HELP_SHOWN}
              onClick={this.toggleHelp}
              id="prose-show-help"
            >
              <Icon name="help" />
              Help
            </Menu.Item>

            <Menu.Menu position="right">
              <CommandsMenu
                getView={() => this._editorView}
                disabled={shown !== EDITOR_SHOWN}
              />
            </Menu.Menu>
          </Menu>
        </div>
      </Sticky>
    );
  }

  render() {
    const { note, shown, uploading } = this.state;

    return (
      <Ref innerRef={this._stickyMenuContextRef}>
        <div className="Editor" ref={this._componentElementRef}>
          {this.renderMenu()}
          {uploading && <div className="Editor--Uploading">Loading....</div>}
          {shown === STATS_SHOWN && (
            <Stats
              todos={this.getTodos()}
              getTextContent={this.getTextContent}
            />
          )}
          {shown === AGENDA_SHOWN && (
            <Agenda todos={this.getTodos()} rerender={this.rerender} />
          )}
          {shown === HELP_SHOWN && this.renderHelp()}
          {shown === SLIDES_SHOWN && <Slides editor={this._editorNode} />}
          <div
            className="EditorWrapper"
            key={note.id + __remountKey}
            ref={this._createEditorView}
            style={shown === EDITOR_SHOWN ? {} : { display: "none" }}
          />
        </div>
      </Ref>
    );
  }
}

export default Editor;
