import moment from "moment";
import React, { useState } from "react";
import OriginalDatePicker from "react-datepicker";
import { Button } from "semantic-ui-react";

import "react-datepicker/dist/react-datepicker.css";

const style = {
  display: "inline-block",
  width: "4rem",
  margin: "2px",
  backgroundColor: "#216ba5",
  borderRadius: ".3rem",
  color: "#fff",
  width: "56px",
  height: "21px",
  cursor: "pointer",
  position: "relative",
  textAlign: "center"
};

export function DatePicker({ selected, onSelect, ...props }) {
  const [value, setValue] = useState(moment(selected));
  const showTime = moment(selected, "YYYY-MM-DD HH:mm", true).isValid();

  return (
    <OriginalDatePicker
      inline
      selected={value.toDate()}
      showTimeInput={showTime}
      shouldCloseOnSelect={false}
      todayButton="Select Today"
      onChange={date => {
        setValue(moment(date));
      }}
      {...props}
    >
      <Button
        positive
        fluid
        onClick={() => {
          onSelect(value.format(showTime ? "YYYY-MM-DD HH:mm" : "YYYY-MM-DD"));
        }}
      >
        Close
      </Button>
    </OriginalDatePicker>
  );
}
