let visibilityChangeHandlers = [];
(() => {
  let hidden, visibilityChange;
  if (typeof document.hidden !== "undefined") {
    // Opera 12.10 and Firefox 18 and later support
    hidden = "hidden";
    visibilityChange = "visibilitychange";
  } else if (typeof document.msHidden !== "undefined") {
    hidden = "msHidden";
    visibilityChange = "msvisibilitychange";
  } else if (typeof document.webkitHidden !== "undefined") {
    hidden = "webkitHidden";
    visibilityChange = "webkitvisibilitychange";
  }

  document.addEventListener(
    visibilityChange,
    () => {
      const isHidden = document[hidden];
      visibilityChangeHandlers.forEach(h => h(isHidden));
    },
    false
  );
})();

export function onVisibilityChange(f) {
  visibilityChangeHandlers.push(f);
  return () => {
    visibilityChangeHandlers = visibilityChangeHandlers.filter(h => h !== f);
  };
}
