import chrono from "chrono-node";

import { Schema, Fragment } from "prosemirror-model";

import { TextSelection } from "prosemirror-state";
import { keymap } from "prosemirror-keymap";
import { gapCursor } from "prosemirror-gapcursor";
import { dropCursor } from "prosemirror-dropcursor";
import { history, undo, redo } from "prosemirror-history";
import {
  selectParentNode,
  baseKeymap,
  chainCommands
} from "prosemirror-commands";

import { InputRule, inputRules } from "prosemirror-inputrules";

import decorations from "./decorations";
import {
  formatDate,
  getZeroBasedNum,
  getDaysInMonthByWeeks,
  monthNumToName
} from "../util";
import {
  splitBlockWithDecorations,
  formatTable,

  // Headers
  toggleHeader,
  selectNextHeader,
  selectPrevHeader,

  // View
  showHelp,
  showStats,
  showSlides,
  showAgenda,
  showEditor,

  // Todo
  todoReset,
  todoPlay,
  todoClearCompleted,

  // Date
  dateSelect,

  // Duration
  durationStart,
  durationContinue,
  durationDouble,

  // Files
  uploadFile,

  // Folded
  foldedToggle,

  // Lists
  indentList,
  unindentList
} from "./commands";

// =============================================================================
// Schema

export const schema = new Schema({
  nodes: {
    doc: {
      content: "block+"
    },

    paragraph: {
      content: "inline*",
      group: "block",
      parseDOM: [{ tag: "p" }],
      toDOM() {
        return ["p", 0];
      }
    },

    text: {
      group: "inline",
      toDOM(node) {
        return node.text;
      }
    },

    hard_break: {
      inline: true,
      group: "inline",
      selectable: false,
      parseDOM: [{ tag: "br" }],
      toDOM: () => ["br"]
    }
  }
});

// =============================================================================
// Plugins

const INPUT_RULES = (function() {
  let rules = [
    new InputRule(/<calendar:?(\s\d\d\d\d-\d\d?)?>/, function(
      state,
      match,
      start,
      end
    ) {
      const { paragraph } = state.schema.nodes;
      const today = match[1] ? new Date(...match[1].split("-"), 0) : new Date();

      const nodes = Fragment.from([
        paragraph.create(
          null,
          schema.text(
            `*${monthNumToName[today.getMonth()]} ${today.getFullYear()}*`
          )
        ),
        paragraph.create(
          null,
          schema.text(
            "|---------|---------|---------|---------|---------|---------|---------|"
          )
        ),
        paragraph.create(
          null,
          schema.text(
            "|  *Mon*  |  *Tue*  |  *Wed*  |  *Thu*  |  *Fri*  |  *Sat*  |  *Sun*  |"
          )
        ),
        paragraph.create(
          null,
          schema.text(
            "|---------|---------|---------|---------|---------|---------|---------|"
          )
        ),
        ...getDaysInMonthByWeeks(
          today.getMonth(),
          today.getFullYear()
        ).map(week =>
          paragraph.create(
            null,
            state.schema.text(
              "| " +
                week
                  .map(d =>
                    d ? `   ${getZeroBasedNum(d.getDate())}   ` : "        "
                  )
                  .join("| ") +
                "|"
            )
          )
        ),
        paragraph.create(
          null,
          schema.text(
            "|---------|---------|---------|---------|---------|---------|---------|"
          )
        )
      ]);

      return state.tr.replaceWith(start, end, nodes);
    }),
    new InputRule(/<[^>]+>/, function(state, match, start, end) {
      const parsed = chrono.parse(match[0])[0];
      if (!parsed || parsed.index !== 1) {
        return null;
      }

      let insert = chrono.parseDate(match[0]);
      insert = formatDate(insert);

      return state.tr.insertText(insert, start, end);
    })
  ];

  return inputRules({ rules });
})();

// =============================================================================
// Plugins

export const plugins = [
  INPUT_RULES,

  history(),
  dropCursor(),
  gapCursor(),

  keymap({
    "Mod-z": undo,
    "Shift-Mod-z": redo,
    "Mod-i": selectParentNode, // TODO: use it to kill row

    "Shift-Enter": baseKeymap["Enter"],
    "Ctrl-Enter": baseKeymap["Enter"], // TODO: add table row
    // Ctrl-l -> clone line

    Tab: chainCommands(formatTable, foldedToggle, indentList, toggleHeader),
    "Shift-Tab": unindentList,

    Enter: splitBlockWithDecorations,

    "Ctrl-1": showEditor,
    "Ctrl-2": showAgenda,
    "Ctrl-3": showSlides,
    "Ctrl-4": showStats,
    "Ctrl-5": showHelp,

    "Ctrl-r": todoReset,
    "Ctrl-Shift-r": todoClearCompleted,

    "Ctrl-u": uploadFile,

    ...(/Mac/.test(navigator.platform)
      ? {
          "Mod-Enter": chainCommands(
            todoPlay,
            dateSelect,
            durationStart,
            baseKeymap["Enter"]
          ),
          "Mod-Shift-Enter": durationContinue,
          "Mod-Shift-8": durationDouble,
          "Mod-ArrowDown": selectNextHeader,
          "Mod-ArrowUp": selectPrevHeader
        }
      : {
          "Ctrl-y": redo,
          "Ctrl-ArrowDown": selectNextHeader,
          "Ctrl-ArrowUp": selectPrevHeader
        })
  }),
  keymap(baseKeymap),

  decorations
];

