// Initialize Firebase
firebase.initializeApp(firebaseConfig);
firebase.analytics();

const uiConfig = {
  // Providers setup
  signInOptions: [
    {
      provider: firebase.auth.GoogleAuthProvider.PROVIDER_ID,
      // Required to enable this provider in one-tap sign-up.
      // authMethod: 'https://accounts.google.com',
      scopes: ["https://www.googleapis.com/auth/contacts.readonly"],
      customParameters: {
        // Forces account selection even when one account
        // is available.
        prompt: "select_account"
      }
    },
    {
      provider: firebase.auth.EmailAuthProvider.PROVIDER_ID,
      requireDisplayName: true
    }
  ],

  // Will use popup for IDP Providers sign-in flow instead of the default, redirect.
  signInFlow: "popup",

  // Terms of service url/callback.
  tosUrl: () => alert("TODO: Terms of service."),
  // Privacy policy url/callback.
  privacyPolicyUrl: () => alert("TODO: PrivacyPolicy"),

  credentialHelper: firebaseui.auth.CredentialHelper.NONE,

  callbacks: {
    // signInSuccess: () => false
    signInSuccessWithAuthResult: function(authResult, redirectUrl) {
      return false;
    }
  }
};

firebase.auth().onAuthStateChanged(user => {
  const container = document.getElementById("root");
  if (user) {
    createStorage(firebase, () => {
      container.innerHTML = "";
      ReactDOM.render(<App firebase={firebase} />, container);
    });

    window.Store = getStorage();
  } else {
    // Initialize the FirebaseUI Widget using Firebase.
    const initUI = () => {
      const ui = new firebaseui.auth.AuthUI(firebase.auth());
      // The start method will wait until the DOM is loaded.
      container.innerHTML = "";
      ui.start("#root", uiConfig);
    };

    ReactDOM.unmountComponentAtNode(container);
    const prevUI = firebaseui.auth.AuthUI.getInstance();
    if (prevUI) {
      prevUI.delete().then(initUI);
    } else {
      initUI();
    }
  }
});

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
