import moment from "moment";
import React from "react";
import ReactDOM from "react-dom";
import {
  List,
  Image,
  Button,
  Dropdown,
  Label,
  Icon,
  Grid
} from "semantic-ui-react";

import { showPopup, hidePopup } from "./popup";
import { PopupTodo } from "./PopupTodo";
import { parseTodoNode, toVisibleSpaces } from "../util";

const Menu = ({ handleClick }) => (
  <>
    <button className="trigger">...</button>
    <div className="options">
      <List selection verticalAlign="middle">
        <List.Item onClick={handleClick} value="play">
          <List.Content floated="right">CTRL+Enter</List.Content>
          <Icon name="video play" color="olive" />
          <List.Content>
            <List.Header>Play</List.Header>
          </List.Content>
        </List.Item>
        <List.Item onClick={handleClick} value="reset">
          <List.Content floated="right">CTRL+R</List.Content>
          <Icon name="retweet" color="olive" />
          <List.Content>
            <List.Header>Reset</List.Header>
          </List.Content>
        </List.Item>
        <List.Item onClick={handleClick} value="delete_completed">
          <List.Content floated="right">CTRL+Shift-R</List.Content>
          <Icon name="delete" color="olive" />
          <List.Content>
            <List.Header>Delete Completed</List.Header>
          </List.Content>
        </List.Item>
        <List.Item onClick={handleClick} value="delete_unfinished">
          <List.Content floated="right"></List.Content>
          <Icon name="delete" color="olive" />
          <List.Content>
            <List.Header>
              Delete
              <br />
              Unfinished
            </List.Header>
          </List.Content>
        </List.Item>
        <List.Item onClick={handleClick} value="mark_today">
          <List.Content floated="right"></List.Content>
          <Icon name="calendar alternate outline" color="olive" />
          <List.Content>
            <List.Header>
              Mark all
              <br /> for Today
            </List.Header>
          </List.Content>
        </List.Item>
      </List>
    </div>
  </>
);

export const WidgetTodo = view => {
  const dom = document.createElement("div");
  dom.classList.add("indicator");
  dom.classList.add("todo-indicator");

  const handleClick = (e, { value: action }) => {
    e.preventDefault();
    const node = e.target;

    const fullTodoList = [dom.parentNode];

    let next;
    while (
      (next = fullTodoList[fullTodoList.length - 1]) &&
      (next = next.nextElementSibling) &&
      !!next.querySelector(".todo")
    ) {
      fullTodoList.push(next);
    }

    switch (action) {
      case "play": {
        const parsedNodes = fullTodoList
          .map(n => parseTodoNode(n))
          .filter(n => !n.done);

        const onFinish = data => {
          data.forEach(({ done, time }, i) => {
            const node = parsedNodes[i];

            if (done && !node.done) {
              node.toggle();
            }

            hidePopup();
          });
        };
        showPopup({
          size: "mini",
          rawContent: true,
          content: <PopupTodo todos={parsedNodes} onFinish={onFinish} />
        });

        return;
      }

      case "reset": {
        fullTodoList
          .map(n => parseTodoNode(n))
          .forEach(n => n.done && n.toggle());
        return;
      }
      case "delete_unfinished":
      case "delete_completed": {
        fullTodoList
          .map(n => parseTodoNode(n))
          .filter(n => (action === "delete_unfinished" ? !n.done : n.done))
          .reverse() // If we don't delete in reverse, prosemirror removes nodes
          .forEach(n => {
            const { node } = n;
            if (!node.parentNode) return;

            const pos = view.posAtDOM(node);
            const pN = view.state.doc.nodeAt(pos);
            const tr = view.state.tr.deleteRange(pos - 2, pos + pN.nodeSize);
            view.dispatch(tr);
          });
        return;
      }

      case "mark_today": {
        fullTodoList
          .map(n => n.querySelector(".date"))
          .filter(Boolean)
          .forEach(n => n.parentNode.removeChild(n));
        fullTodoList.forEach(n => {
          const date = toVisibleSpaces(" ") + moment().format("YYYY-MM-DD");
          const dateNode = document.createTextNode(date);
          n.appendChild(dateNode);
        });
        return;
      }
      default:
        return;
    }
  };

  ReactDOM.render(<Menu {...{ handleClick }} />, dom);

  return dom;
};
