import React from "react";
import ReactDOM from "react-dom";
import hljs from "highlight.js";
import classNames from "classnames";
import { Plugin } from "prosemirror-state";
import { Decoration, DecorationSet } from "prosemirror-view";
import { createPopper } from "@popperjs/core";

import Sound from "../Sound";
import { DatePicker } from "../DatePicker/DatePicker";
import { WidgetTodo } from "./WidgetTodo";
import { WidgetDuration } from "./WidgetDuration";
import { hidePopup, showPopup } from "./popup";
import {
  formatDate,
  parseDuration,
  getTextContent,
  toVisibleSpaces
} from "../util";

import "highlight.js/styles/arta.css";

/**
 * Decorations that require the same open and close strings
 * and capture all the lines in between.
 *
 * NOTE: These work on the wrapper node, not on the text node.
 */
export const OpenCloseDecorations = {
  code: /^```/gi,
  folded: /^\.\.\.$/gi
};

export const WholeNodeDecorations = {
  // NOTE: Table row is also handled repeatably.
  tableRow: /^\|(?:\s|-|=).*$/gi,
  _heading: /^(#{1,5})\s.*$/gi // Underscored names are skipped.
};

export const RepeatableDecorations = {
  quote: /^>\s/gi,
  bullet: /^\s*\*\s/gi,
  ordered: /^\s*(\d+)\.\s/gi,
  _todo: /^(-|\+)\s/gi
};

function genInlineDecorationRegex(char) {
  return new RegExp(
    `\\${char}(:?[^\\s\\${char}][^\\${char}]*[^\\s\\${char}]|[^\\s\\${char}])\\${char}(?=\\s|$)`,
    "gi"
  );
}
/**
 * These work on the text node.
 */
export const Decorations = {
  hr: /^---$/gi,

  bold: genInlineDecorationRegex("*"),
  italic: genInlineDecorationRegex("/"),
  highlight: genInlineDecorationRegex("`"),
  underline: genInlineDecorationRegex("_"),
  striketrough: genInlineDecorationRegex("~"),

  toggleFolded: /^\.\.\.\.$/gi,

  // Alternative date without widget.
  date: new RegExp(
    "(" +
      //
      "everyday" +
      "|mon-tue" +
      "|mon-wed" +
      "|mon-thu" +
      "|mon-fri" +
      "|mon-sat" +
      "|mon-sun" +
      "|tue-wed" +
      "|tue-thu" +
      "|tue-fri" +
      "|tue-sat" +
      "|tue-sun" +
      "|wed-thu" +
      "|wed-fri" +
      "|wed-sat" +
      "|wed-sun" +
      "|thu-fri" +
      "|thu-sat" +
      "|thu-sun" +
      "|fri-sat" +
      "|fri-sun" +
      "|sat-sun" +
      "|mon" +
      "|tue" +
      "|wed" +
      "|thu" +
      "|fri" +
      "|sat" +
      "|sun" +
      ")" +
      "(:?\\s(\\d\\d):(\\d\\d))?\\b",
    "gi"
  ),

  // NOTE: If name starts with `_`, standard decoration won't be
  // created. This can be used if we some special handling in
  // with DecorationsWidgets function.
  _url: /([^\S]|^)(((https?:\/\/)|(www\.))(\S+))/gi,
  _img: /(\s|^)\[(https:\/\/[^\]]+)\](?=\s|$)/gi,
  _tag: /(\s|^)#[^\b\s#\-_`]+/gi,
  _link: /\[([\w\s\d]+)\]\((https?:\/\/[\w\d./?=#]+)\)/gi,
  _date: /\s(\d\d\d\d)-(\d\d)-(\d\d)(:?\s(\d\d):(\d\d))?\b/gi,
  _duration: /\s(\d\d?[hms])(\d\d?[ms])?(\d\d?s)?\b/gi,
  _checkbox: /(\s|^)\[(\+|-)\](\s|$)/gi,

  _counter: /(\s|^)\[(\d+)\](\s|$)/gi,
  _proportion: /(\s|^)\[(\d+)\/(\d+)\](\s|$)/gi,

  ...RepeatableDecorations
};

/**
 * Some decorations require additional widgets.
 */
export const DecorationsWidgets = {
  _todo: getTodoWidgets,
  _duration: getDurationWidgets,
  _date: getDateWidgets,
  _url: getUrlWidgets,
  _tag: getTagWidgets,
  _link: getLinkWidgets,
  _img: getImgWidgets,
  _checkbox: getCheckboxWidgets,
  _counter: getCounterWidgets,
  _proportion: getCounterWidgets,
  _heading: getHeadingWidgets
};

/**
 * Decorations won't be visible inside specified nodes.
 */
const excludedNodes = {};

/**
 * We don't want some of the decorations inside a table.
 */
const excludedInTable = {
  _counter: true,
  _proportion: true
};

/**
 * Handlers are delegated (live) events on click.
 *
 * className -> handler(e, element, view)
 */
export const decorationsHandlers = {};

function getDecorations(doc) {
  let decos = [];
  let openClose = {};

  doc.descendants((node, pos) => {
    if (excludedNodes[doc.resolve(pos).node().type.name]) return;

    if (node.isText) {
      const isTableRow = !!node.text.match(WholeNodeDecorations["tableRow"]);
      Object.keys(Decorations).forEach(name => {
        const regex = Decorations[name];
        while (true) {
          let m = regex.exec(node.text);
          if (!m) return;

          if (excludedInTable[name] && isTableRow) {
            return;
          }

          if (name[0] !== "_") {
            decos.push(
              Decoration.inline(
                pos + m.index,
                pos +
                  m.index +
                  m[0].length +
                  (RepeatableDecorations[name] ? -1 : 0), // TODO:
                {
                  class: name
                }
              )
            );
          }

          if (DecorationsWidgets[name]) {
            decos.push(...DecorationsWidgets[name](m, pos, doc, node));
          }
        }
      });

      return;
    }

    Object.keys(WholeNodeDecorations).forEach(name => {
      const regex = WholeNodeDecorations[name];
      while (true) {
        let m = regex.exec(getTextContent(node));
        if (!m) return;

        if (name[0] !== "_") {
          decos.push(
            Decoration.node(pos + m.index, pos + m.index + node.nodeSize, {
              class: name
            })
          );
        }

        if (DecorationsWidgets[name]) {
          decos.push(...DecorationsWidgets[name](m, pos, doc, node));
        }
      }
    });

    // NOTE: Every header resets `openClose` pairs.
    if (/^(#{1,5})\s.*$/gi.test(getTextContent(node))) {
      openClose = {};
    }
    Object.keys(OpenCloseDecorations).forEach(name => {
      const regex = OpenCloseDecorations[name];
      while (true) {
        const textContent = getTextContent(node);
        let m = regex.exec(textContent);
        if (!m) return;

        if (!openClose[name]) {
          openClose[name] = { node, pos };
          continue;
        }

        if (openClose[name]) {
          const closing = { node, pos };
          const opening = openClose[name];

          const textContent = [];
          doc.nodesBetween(opening.pos, closing.pos + 1, (n, p) => {
            if (n.isText) return;
            textContent.push(getTextContent(n));

            decos.push(
              Decoration.node(p, p + n.nodeSize, {
                class: classNames(
                  name,
                  p === opening.pos && `${name}-opening`,
                  p === closing.pos && `${name}-closing`
                )
              })
            );
          });

          delete openClose[name];

          if (name === "code") {
            decos.push(
              Decoration.widget(
                opening.pos + 1,
                (view, getPos) => {
                  const dom = document.createElement("pre");
                  const lang = textContent[0].replace("```", "");
                  const source = hljs.getLanguage(lang)
                    ? hljs.highlight(lang, textContent.slice(1, -1).join("\n"))
                        .value
                    : textContent.slice(1, -1).join("\n");
                  dom.innerHTML = `${textContent[0]}\n${source}\n${
                    textContent[textContent.length - 1]
                  }`;

                  dom.classList.add("code-highlight");

                  return dom;
                },
                {
                  key: textContent.join("\n")
                }
              )
            );
          }
          continue;
        }
      }
    });
  });

  return DecorationSet.create(doc, decos);
}

// =============================================================================
// Widgets

function getUrlWidgets(m, pos, _, node) {
  return [
    Decoration.inline(
      pos + m.index + m[1].length,
      pos + m.index + m[0].length,
      {
        class: "url",
        nodeName: "a",
        href: node.text,
        title: node.text
      }
    )
  ];
}

function getLinkWidgets(m, pos, _, node) {
  return [
    Decoration.inline(pos + m.index, pos + m.index + 1, {
      class: "link--bracket"
    }),
    Decoration.inline(pos + m.index + 1, pos + m.index + 1 + m[1].length, {
      class: "url",
      nodeName: "a",
      href: m[2]
    }),
    Decoration.inline(
      pos + m.index + 1 + m[1].length,
      pos + m.index + 1 + m[1].length + 2,
      {
        class: "link--bracket"
      }
    ),
    Decoration.inline(
      pos + m.index + 1 + m[1].length + 2,
      pos + m.index + 1 + m[1].length + 2 + m[2].length,
      {
        class: "link--symbol",
        nodeName: "input",
        type: "button",
        value: "🔗"
      }
    ),
    Decoration.inline(
      pos + m.index + 1 + m[1].length + 2 + m[2].length,
      pos + m.index + 1 + m[1].length + 2 + m[2].length + 1,
      {
        class: "link--bracket"
      }
    )
  ];
}

decorationsHandlers["folded-opening"] = (e, elm, view) => {
  e.preventDefault();
  e.stopPropagation();
  elm.textContent = "....";
};

decorationsHandlers["toggleFolded"] = (e, elm, view) => {
  e.preventDefault();
  e.stopPropagation();
  elm.textContent = "...";
};

decorationsHandlers["todo"] = (e, elm, view) => {
  e.stopPropagation();

  const pos = elm.dataset["pos"] * 1;
  Sound.click();
  view.dispatch(
    view.state.tr.insertText(elm.checked ? "+" : "-", pos, pos + 1)
  );
};
function getTodoWidgets(m, pos, doc) {
  const decos = [
    Decoration.inline(pos + m.index, pos + m.index + m[0].length - 1, {
      class: "todo",
      nodeName: "input",
      type: "checkbox",
      "data-pos": pos + m.index,
      ...(m[1] === "+" ? { checked: true } : {})
    })
  ];
  const currentNode = doc.resolve(pos).node();
  const nodeBefore = doc.resolve(pos - 1).nodeBefore;
  const nodeAfter =
    doc.content.size <= pos + currentNode.nodeSize
      ? null
      : doc.resolve(pos + currentNode.nodeSize).node();

  // NOTE: Todo widget should be applied right next to the first Todo
  // item in the group.
  const todoRegEx = /(\+|-)\s/;
  if (
    (!nodeBefore || !todoRegEx.test(getTextContent(nodeBefore).slice(0, 2))) &&
    nodeAfter &&
    todoRegEx.test(getTextContent(nodeAfter).slice(0, 2))
  ) {
    decos.push(
      Decoration.widget(pos + m.index, view => WidgetTodo(view), {
        key: getTextContent(currentNode)
      })
    );
  }

  return decos;
}

decorationsHandlers["duration--button"] = (e, elm, view) => {
  e.preventDefault();
  e.stopPropagation();

  const pos = elm.dataset["pos"] * 1;
  const text = getTextContent(elm.nextElementSibling);
  const match = parseDuration(text);

  const overlay = document.createElement("div");
  overlay.classList.add("duration---overlay");

  const menu = document.createElement("div");
  menu.classList.add("duration--menu");
  ReactDOM.render(<WidgetDuration {...{ view, pos, match, text }} />, menu);

  overlay.addEventListener("click", e => {
    if (overlay.parentNode) {
      overlay.parentNode.removeChild(overlay);
    }
  });

  overlay.appendChild(menu);
  document.body.appendChild(overlay);

  createPopper(elm, menu, { placement: "bottom-start" });
};

function getDurationWidgets(m, pos, _, node) {
  const isInTableRow = !!node.text.match(WholeNodeDecorations["tableRow"]);
  return [
    ...(isInTableRow
      ? []
      : [
          Decoration.inline(pos + m.index, pos + m.index + 1, {
            class: "duration--button",
            nodeName: "input",
            type: "button",
            value: "▼",
            "data-pos": pos + m.index
          })
        ]),
    Decoration.inline(pos + m.index + 1, pos + m.index + m[0].length, {
      class: "duration"
    })
  ];
}

decorationsHandlers["date--button"] = (e, elm, view) => {
  e.preventDefault();
  e.stopPropagation();

  const startPos = elm.dataset["startpos"] * 1;
  const endPos = elm.dataset["endpos"] * 1;
  const selected = getTextContent(elm.nextElementSibling);

  showPopup({
    size: "mini",
    basic: true,
    closeIcon: false,
    content: (
      <DatePicker
        selected={selected}
        onSelect={date => {
          view.dispatch(view.state.tr.insertText(" " + date, startPos, endPos));
          hidePopup();
        }}
      />
    )
  });
};

function getDateWidgets(m, pos, _, node) {
  const isInTableRow = !!node.text.match(WholeNodeDecorations["tableRow"]);
  return [
    ...(isInTableRow
      ? []
      : [
          Decoration.inline(pos + m.index, pos + m.index + 1, {
            class: "date--button",
            nodeName: "input",
            type: "button",
            value: "▼",
            "data-startpos": pos + m.index,
            "data-endpos": pos + m.index + m[0].length
          })
        ]),
    Decoration.inline(pos + m.index + 1, pos + m.index + m[0].length, {
      class: "date"
    })
  ];
}

function getTagWidgets(m, pos) {
  return [
    Decoration.inline(
      pos + m.index + m[1].length,
      pos + m.index + m[0].length,
      {
        class: "tag"
      }
    )
  ];
}

decorationsHandlers["checkbox"] = (e, elm, view) => {
  e.stopPropagation();

  const pos = elm.dataset.pos * 1;
  Sound.click();
  view.dispatch(
    view.state.tr.insertText(elm.checked ? "+" : "-", pos + 1, pos + 2)
  );
};

function getCheckboxWidgets(m, pos) {
  return [
    Decoration.inline(
      pos + m.index + m[1].length,
      pos + m.index + m[0].length - m[3].length,
      {
        class: "checkbox",
        nodeName: "input",
        type: "checkbox",
        "data-pos": pos + m.index + m[1].length,
        ...(m[2] === "+" ? { checked: true } : {})
      }
    )
  ];
}

decorationsHandlers["counter--left"] = (e, elm, view) => {
  Sound.click();
  const pos = elm.dataset.pos * 1;
  const m = getTextContent(elm.nextElementSibling).split("/");

  view.dispatch(
    view.state.tr.insertText(
      `[${Math.max(m[0] * 1 - 1, 0)}${m[1] ? `/${m[1]}` : ""}]`,
      pos,
      pos + m.join("/").length + 2
    )
  );
};

decorationsHandlers["counter--right"] = (e, elm, view) => {
  e.preventDefault();
  e.stopPropagation();

  Sound.click();
  const pos = elm.dataset.pos * 1;
  const m = getTextContent(elm.previousElementSibling).split("/");

  view.dispatch(
    view.state.tr.insertText(
      `[${Math.min(m[0] * 1 + 1, m[1] ? m[1] : Infinity)}${
        m[1] ? `/${m[1]}` : ""
      }]`,
      pos,
      pos + m.join("/").length + 2
    )
  );
};

function getCounterWidgets(m, pos) {
  const start = pos + m.index + m[1].length;
  const end =
    pos +
    m.index +
    m[0].length -
    ((m.length === 5 && m[4].length) || (m.length === 4 && m[3].length)
      ? 1
      : 0);

  return [
    Decoration.inline(start, start + 1, {
      class: "counter--button counter--left",
      nodeName: "input",
      type: "button",
      value: "◀",
      "data-pos": start
    }),
    Decoration.inline(start + 1, end - 1, {
      class: "counter"
    }),
    Decoration.inline(end - 1, end, {
      class: "counter--button counter--right",
      nodeName: "input",
      type: "button",
      value: "►",
      "data-pos": start
    })
  ];
}

decorationsHandlers["heading--button"] = (e, elm, view) => {
  e.preventDefault();
  e.stopPropagation();

  Sound.click();
  const pos = elm.dataset.pos * 1;
  const text = getTextContent(elm.parentNode);
  const level = text.match(/^#+/)[0];

  view.dispatch(
    view.state.tr.insertText(
      level.length === 5 ? `#` : `#${level}`,
      pos,
      pos + level.length + 1
    )
  );
};

function getHeadingWidgets(m, pos, _, node) {
  return [
    Decoration.node(pos + m.index, pos + m.index + node.nodeSize, {
      class: `heading heading-${m[1].length}`
    }),
    Decoration.inline(pos + m.index + 1, pos + m.index + 1 + m[1].length, {
      class: "heading--button",
      type: "button",
      nodeName: "input",
      value: `H${m[1].length}`,
      "data-pos": pos
    })
  ];
}

function getImgWidgets(m, pos) {
  return [
    Decoration.inline(
      pos + m.index + m[1].length,
      pos + m.index + m[0].length,
      {
        class: "img",
        nodeName: "img",
        src: m[2]
      }
    )
  ];
}

// =============================================================================
// Plugin

export default new Plugin({
  state: {
    init(_, { doc }) {
      return getDecorations(doc);
    },
    apply(tr, decorationsSet) {
      return tr.docChanged ? getDecorations(tr.doc) : decorationsSet;
    }
  },
  props: {
    decorations(state) {
      return this.getState(state);
    }
  }
});
