import initialNotes from "./initial.js";
import { showError } from "./errors";

const LOCAL_STORAGE_STATE_KEY = "__prose-editor-in-memory-store__";
const LOCAL_STORAGE_SELECTED_KEY = "__prose-editor-in-memory-store-selected__";

function getNoteTemplate(id, content = null, noteData = {}) {
  return {
    id,
    content,
    meta: {
      title: null,
      tags: []
    },
    created_at: new Date(),
    updated_at: new Date(),
    deleted_at: null,
    ...noteData
  };
}

// TODO: Move to pro in a callback.
export class Storage {
  constructor(firebase, callback) {
    this._userId = firebase.auth().currentUser.uid;
    this._firebase = firebase;

    this._handlers = []; // Storage for update handlers.

    // Selected note is not part of the state as user may have multiple
    // app instances at the same time. It is kept only in local storage.
    this._selectedNote =
      window.localStorage.getItem(LOCAL_STORAGE_SELECTED_KEY) || null;

    // State is the data persisted in Firebase.
    this.state = window.localStorage.getItem(LOCAL_STORAGE_STATE_KEY)
      ? JSON.parse(window.localStorage.getItem(LOCAL_STORAGE_STATE_KEY))
      : {
          notes: initialNotes.reduce((result, { content, ...noteData }, i) => {
            result[i + 1] = getNoteTemplate(i + 1, content, noteData);
            return result;
          }, {})
        };

    if (Object.keys(this.state.notes).length === 0) {
      this.addNewNote();
    }

    if (!this.noteExists(this._selectedNote)) {
      this.selectNote(Math.max(...Object.keys(this.state.notes)), true);
    }

    window.sss = this;

    setTimeout(() => {
      callback();
    }, 10);
  }

  // ===========================================================================
  // Subscription

  subscribe(f) {
    this._handlers.push(f);
    return () => {
      this._handlers = this._handlers.filter(fn => fn !== f);
    };
  }

  handleUpdate(...args) {
    if (!this.noteExists(this._selectedNote)) {
      this.selectNote(Math.max(...Object.keys(this.state.notes)), true);
    }

    if (Object.keys(this.state.notes).length === 0) {
      this.addNewNote();
    }

    window.localStorage.setItem(
      LOCAL_STORAGE_STATE_KEY,
      JSON.stringify(this.state)
    );

    this._handlers.forEach(h => h(...args));
  }

  // ===========================================================================
  // Selectors

  getSelectedNote() {
    return this.state.notes[this._selectedNote];
  }

  getSelectedNoteContent() {
    return this.getSelectedNote().content;
  }

  noteExists(id) {
    return !!this.state.notes[id];
  }

  getNextNoteId() {
    return Math.max(...Object.keys(this.state.notes), 0) + 1;
  }

  getNotesList() {
    return Object.values(this.state.notes).sort(
      (a, b) => b.updated_at.seconds - a.updated_at.seconds
    );
  }

  getActiveNotes() {
    return this.getNotesList().filter(n => !n.deleted_at);
  }

  getInactiveNotes() {
    return this.getNotesList().filter(n => !!n.deleted_at);
  }

  getNotesByTagsAndDate() {
    const result = {};
    this.getActiveNotes()
      .sort((a, b) => a.updated_at - b.updated_at)
      .forEach(note => {
        note.meta.tags.forEach(tag => {
          if (!result[tag]) result[tag] = [];
          result[tag].push(note);
        });
      });

    return result;
  }

  // ===========================================================================
  // Mutations

  selectNote = id => {
    if (id === this.selectNote) return;

    if (!this.state.notes[id]) {
      throw new Error(`No note with id: ${id}`);
    }

    this._selectedNote = id;
    window.localStorage.setItem(LOCAL_STORAGE_SELECTED_KEY, id);
    this.handleUpdate();
  };

  addNewNote = () => {
    const id = this.getNextNoteId();
    const note = getNoteTemplate(id);

    this.state.notes[id] = note;
    this.selectNote(id);
  };

  updateNote = (id, data, content) => {
    const updateData = {
      ...data,
      content,
      updated_at: this._firebase.firestore.Timestamp.now()
    };

    this.state.notes[id] = {
      ...this.state.notes[id],
      ...updateData
    };

    this.handleUpdate();
  };

  deleteNote = id => {
    this.state.notes[id] = {
      ...this.state.notes[id],
      deleted_at: this._firebase.firestore.Timestamp.now()
    };
    this.handleUpdate();
  };

  restoreNote = id => {
    this.state.notes[id] = {
      ...this.state.notes[id],
      deleted_at: null
    };
    this.handleUpdate();
  };

  permanentlyDeleteNote = id => {
    delete this.state.notes[id];
    this.handleUpdate();
  };

  // ===========================================================================
  // Files

  uploadFile = () => {
    return Promise.reject(
      new Error("Uploading files is not supported in the free version.")
    );
  };
}

// =============================================================================
// Exports

let instance = null;

export function createStorage(...args) {
  return (instance = new Storage(...args));
}

export function getStorage() {
  if (!instance) {
    throw new Error("Create store before using it");
  }
  return instance;
}
