import React, { useState } from "react";
import moment from "moment";
import classNames from "classnames";

import {
  List,
  Label,
  Grid,
  Menu,
  Icon,
  Checkbox,
  Header,
  Message,
  Button
} from "semantic-ui-react";

import {
  parseDuration,
  normalizeDuration,
  getZeroBasedNum,
  matchRecurringDay
} from "../util";

import { Decorations } from "../Editor/decorations";

import "./Agenda.css";

const matchDate = d => {
  Decorations._date.lastIndex = 0;
  const match = Decorations._date.exec(" " + d);
  Decorations._date.lastIndex = 0;
  return match;
};

const matchRecurringDate = d => {
  Decorations.date.lastIndex = 0;
  const match = Decorations.date.exec(d);
  Decorations.date.lastIndex = 0;
  return match;
};

function processTodos(todos, today = moment().startOf("day")) {
  const forToday = todos
    .filter(item => {
      if (!item.date) return false;

      const recurring = matchRecurringDate(item.date);
      if (recurring) {
        const dayNum = today.day();
        const dayStr = recurring[1];

        return matchRecurringDay(dayNum, dayStr);
      }

      return moment(item.date).isSame(today, "day");
    })
    .map(item => {
      const result = { ...item };
      let hours, minutes;

      const recurring = matchRecurringDate(item.date);
      if (recurring) {
        [hours, minutes] = recurring.slice(3);
      } else {
        [hours, minutes] = matchDate(item.date).slice(5);
      }

      // Date has no hours and minutes.
      if (hours === undefined) return result;

      result.hours = hours;
      result.minutes = minutes;
      result.fromTime = moment.duration({
        minutes,
        hours
      });

      if (result.duration) {
        const d = parseDuration(result.duration);

        d.h += hours * 1;
        d.m += minutes * 1;
        normalizeDuration(d);

        result.toTime = moment.duration({
          minutes: d.m,
          hours: d.h
        });
      }

      return result;
    });

  if (forToday.length === 0) {
    return null;
  }

  const noHour = forToday.filter(item => {
    return !item.fromTime;
  });

  const withHour = forToday
    .filter(item => !!item.fromTime)
    .sort((a, b) => {
      const { hours: hoursA, minutes: minutesA } = a;
      const { hours: hoursB, minutes: minutesB } = b;
      return hoursA * 60 + minutesA * 1 - (hoursB * 60 + minutesB * 1);
    });

  return { withHour, noHour };
}

function durationToStr(d) {
  return `${getZeroBasedNum(d.hours())}:${getZeroBasedNum(d.minutes())}`;
}

const SMALL_PADDING = "---------------------";
const BIG_PADDING =
  "------------------------ --------- ---------- --------------- ------------ -------- ----- ----";

export default function Agenda({ todos, rerender }) {
  const [date, setDate] = useState(moment().startOf("day"));
  const groups = processTodos(todos, date);

  const menu = (
    <Menu fluid widths={3}>
      <Menu.Item
        as="a"
        icon
        onClick={() => setDate(date.clone().add(1, "day"))}
      >
        <Icon name="chevron left" />
      </Menu.Item>

      <Menu.Item as="a" icon disabled>
        {date.format("dddd, MMMM Do YYYY")}
      </Menu.Item>

      <Menu.Item
        as="a"
        icon
        onClick={() => setDate(date.clone().subtract(1, "day"))}
      >
        <Icon name="chevron right" />
      </Menu.Item>
    </Menu>
  );

  if (!groups) {
    return (
      <div className="Editor--agenda">
        {menu}
        <Header
          as="h2"
          content={`Agenda`}
          subheader={` for ${moment().format("dddd, MMMM Do YYYY")}`}
        />
        <Message info>No items for today.</Message>
      </div>
    );
  }

  const { withHour, noHour } = groups;
  let prevTime = moment.duration({ hours: 0, minutes: 0 });

  return (
    <div className="Editor--agenda">
      {menu}
      <Grid columns={2} relaxed="very" doubling>
        <Grid.Column>
          <Header
            as="h2"
            content={`Agenda`}
            subheader={` for ${date.format("dddd, MMMM Do YYYY")}`}
          />
          <Button
            icon
            labelPosition="left"
            compact
            onClick={() => {
              withHour.forEach(item => item.done && item.toggle());
              rerender();
            }}
          >
            <Icon name="retweet" />
            Reset
          </Button>
          <List divided selection>
            {withHour.map((item, i) => {
              const { fromTime, toTime } = item;
              let spaceTime = null;
              if (fromTime.asMinutes() - prevTime.asMinutes() >= 30) {
                spaceTime = (
                  <List.Item key={prevTime} className="Agenda--Row">
                    <Label
                      as="a"
                      basic
                      color="grey"
                      className="Agenda--TimeLabel"
                    >
                      {durationToStr(prevTime)}-{durationToStr(fromTime)}
                    </Label>
                    {durationToStr(prevTime) === "00:00"
                      ? BIG_PADDING
                      : SMALL_PADDING}
                  </List.Item>
                );
              }
              prevTime = toTime || fromTime;
              return (
                <React.Fragment key={i}>
                  {spaceTime}
                  <List.Item className="Agenda--Row">
                    <Label
                      as="a"
                      basic
                      color={item.done ? "olive" : "orange"}
                      className="Agenda--TimeLabel"
                    >
                      {durationToStr(fromTime)}
                      {toTime ? `-${durationToStr(toTime)}` : "......"}
                    </Label>
                    <Checkbox
                      className="Agenda--Checkbox"
                      checked={item.done}
                      onClick={() => {
                        item.toggle();
                        rerender();
                      }}
                    />
                    <span
                      className={classNames(
                        "Agenda--Checkbox--Label",
                        item.done && "done"
                      )}
                    >
                      {item.text}
                    </span>
                  </List.Item>
                </React.Fragment>
              );
            })}
            {durationToStr(prevTime) !== "23:59" && (
              <List.Item key="last" className="Agenda--Row">
                <Label as="a" basic color="grey" className="Agenda--TimeLabel">
                  {durationToStr(prevTime)}-23:59
                </Label>
                <span>{BIG_PADDING}</span>
              </List.Item>
            )}
          </List>
        </Grid.Column>
        <Grid.Column>
          <Header
            as="h2"
            content="Tasks for the day"
            subheader="Tasks that don't have specific time attached to them"
          />
          {noHour.length === 0 ? (
            <Message visible className="Menu--Notes--Message">
              No tasks for today.
            </Message>
          ) : (
            <List selection>
              {noHour.map((item, i) => (
                <List.Item key={i} className="Agenda--Row">
                  <Checkbox
                    className="Agenda--Checkbox"
                    defaultChecked={item.done}
                    onClick={() => {
                      item.toggle();
                      rerender();
                    }}
                  />
                  <span
                    className={classNames(
                      "Agenda--Checkbox--Label",
                      item.done && "done"
                    )}
                  >
                    {item.text}
                  </span>
                </List.Item>
              ))}
            </List>
          )}
        </Grid.Column>
      </Grid>
    </div>
  );
}
