import moment from "moment";

export function formatDate(date) {
  let d = new Date(date),
    month = "" + (d.getMonth() + 1),
    day = "" + d.getDate(),
    year = d.getFullYear();

  if (month.length < 2) month = "0" + month;
  if (day.length < 2) day = "0" + day;

  return [year, month, day].join("-");
}

export function getDaysInMonth(month, year) {
  const date = new Date(Date.UTC(year, month, 1));
  const days = [];
  while (date.getMonth() === month) {
    days.push(new Date(date));
    date.setDate(date.getDate() + 1);
  }
  return days;
}

export function getDaysInMonthByWeeks(...args) {
  const days = getDaysInMonth(...args);
  const weeks = [];

  let currentWeek = 0;
  days.forEach(day => {
    if (!weeks[currentWeek]) weeks[currentWeek] = [];
    weeks[currentWeek].push(day);

    if (day.getDay() === 0) {
      currentWeek++;
    }
  });
  const maxLen = Math.max(...weeks.map(w => w.length));

  while (weeks[0].length !== maxLen) {
    weeks[0].unshift(null);
  }

  while (weeks[weeks.length - 1].length !== maxLen) {
    weeks[weeks.length - 1].push(null);
  }

  return weeks;
}

export function getZeroBasedNum(num) {
  return num < 10 ? `0${num}` : num;
}

