// TODO: refactor
export const formatDateTime = item => {
  return moment(item.seconds * 1000).format("MMMM Do YYYY, h:mm:ss a");
};

export const EVERYDAY = "everyday";
export const DAYS_OF_THE_WEEK = [
  "mon",
  "tue",
  "wed",
  "thu",
  "fri",
  "sat",
  "sun"
];

export const matchRecurringDay = (_dayOfTheWeekNum, dayOfTheWeekStr) => {
  const dayOfTheWeekNum = _dayOfTheWeekNum === 0 ? 6 : _dayOfTheWeekNum - 1;
  if (dayOfTheWeekStr === EVERYDAY) return true;

  const dayIndex = DAYS_OF_THE_WEEK.indexOf(dayOfTheWeekStr);
  if (dayIndex !== -1) {
    return dayOfTheWeekNum === dayIndex;
  }

  const [fromDay, toDay] = dayOfTheWeekStr.split("-");
  if (!fromDay || !toDay) return false;

  const fromDayIndex = DAYS_OF_THE_WEEK.indexOf(fromDay);
  const toDayIndex = DAYS_OF_THE_WEEK.indexOf(toDay);
  if (fromDayIndex === -1 || toDayIndex === -1) return false;

  return fromDayIndex <= dayOfTheWeekNum && dayOfTheWeekNum <= toDayIndex;
};

// =============================================================================
// Duration

export function msToDuration(ms) {
  const d = {
    h: 0,
    m: 0,
    s: Math.round(ms / 1000)
  };
  normalizeDuration(d);
  return d;
}

export function parseDuration(str) {
  return parseDurationMatch(str.match(/\b(\d\d?[hms])(\d\d?[ms])?(\d\d?s)?\b/));
}

export function parseDurationMatch(match) {
  return match
    .slice(1)
    .filter(Boolean)
    .reduce(
      (result, item) => {
        result[item[item.length - 1]] += parseInt(item, 10) * 1;
        return result;
      },
      {
        h: 0,
        m: 0,
        s: 0
      }
    );
}

export function normalizeDuration(d) {
  if (d.s >= 60) {
    d.m += Math.floor(d.s / 60);
    d.s = d.s % 60;
  }

  if (d.m >= 60) {
    d.h += Math.floor(d.m / 60);
    d.m = d.m % 60;
  }
}

export function durationToStr(d) {
  return (
    `${d.h ? d.h + "h" : ""}${d.m ? d.m + "m" : ""}${d.s ? d.s + "s" : ""}` ||
    "0s"
  );
}

// =============================================================================
// Text

export function getTextContent(n) {
  return n.textContent.split("\u00a0").join(" ");
}

export function toVisibleSpaces(text) {
  return text.split(" ").join("\u00a0");
}

export function toRealSpaces(text) {
  return text.split("\u00a0").join(" ");
}

// =============================================================================
// Todo

export function parseTodoNode(n) {
  const date = n.querySelector(".date");
  const duration = n.querySelector(".duration");

  const clone = n.cloneNode(true);
  Array.from(clone.children)
    .filter(
      c =>
        c.classList.contains("ProseMirror-widget") ||
        // Clean extracted data
        c.classList.contains("duration") ||
        c.classList.contains("date") ||
        c.classList.contains("img") ||
        c.classList.contains("img-text")
    )
    .forEach(c => c.parentNode.removeChild(c));

  return {
    node: n,
    done: clone.innerText[0] === "+",
    text: toRealSpaces(clone.innerText.replace(/^(-|\+)\s/, "")),
    date: date && toRealSpaces(date.innerText),
    duration: duration && duration.innerText,

    images: Array.from(n.querySelectorAll(".img-text")).map(item =>
      item.innerText.replace(/(\[|\])/g, "").trim()
    ),

    toggle: () => n.querySelector("input[type=checkbox]").click(),
    toJSON() {
      const result = { ...this };
      delete result.node;
      delete result.toggle;
      delete result.toJSON;

      return result;
    }
  };
}
