import moment from "moment";
import ReactDOM from "react-dom";
import React, { useState, useEffect } from "react";
import { Label, Icon, Grid, List } from "semantic-ui-react";

import Sound from "../Sound";
import { showPopup } from "./popup";
import {
  getZeroBasedNum,
  durationToStr,
  normalizeDuration,
  parseDurationMatch
} from "../util";

const TimeTracker = ({ duration: initial, currentTimeRef }) => {
  const [duration, setDuration] = useState(
    moment.duration({
      seconds: initial.s,
      minutes: initial.m,
      hours: initial.h
    })
  );

  useEffect(() => {
    const timer = setTimeout(() => {
      currentTimeRef.value = duration.clone().add(moment.duration(1, "s"));
      setDuration(currentTimeRef.value);
    }, 1000);
    return () => {
      clearTimeout(timer);
    };
  });

  return (
    <Grid textAlign="center" columns={1}>
      <Grid.Column>
        <Label circular as="span" size="massive" color="olive">
          <Icon name="stopwatch" />
          {duration.hours() ? getZeroBasedNum(duration.hours()) : ""}
          {getZeroBasedNum(duration.minutes())}:
          {getZeroBasedNum(duration.seconds())}
        </Label>
      </Grid.Column>
    </Grid>
  );
};

const Timer = ({ duration: initial }) => {
  const [duration, setDuration] = useState(
    moment.duration({
      seconds: initial.s,
      minutes: initial.m,
      hours: initial.h
    })
  );

  useEffect(() => {
    if (duration.asSeconds() === 0) {
      Sound.alarm();
      Sound.alarm();
      return () => {};
    }

    const timer = setTimeout(() => {
      setDuration(duration.clone().subtract(moment.duration(1, "s")));
    }, 1000);
    return () => {
      clearTimeout(timer);
    };
  });

  return (
    <Grid textAlign="center" columns={1}>
      <Grid.Column>
        <Label circular as="span" size="massive" color="olive">
          <Icon name={duration.asSeconds() === 0 ? "check" : "stopwatch"} />
          {duration.hours() ? getZeroBasedNum(duration.hours()) : ""}
          {getZeroBasedNum(duration.minutes())}:
          {getZeroBasedNum(duration.seconds())}
        </Label>
      </Grid.Column>
    </Grid>
  );
};

export const WidgetDuration = ({ match, view, pos, text: prevText }) => {
  const handleContinue = () => {
    const currentTimeRef = { value: null };
    showPopup({
      size: "mini",
      content: <TimeTracker duration={match} currentTimeRef={currentTimeRef} />,
      actions: [
        {
          text: "Stop",
          fn: "hide"
        }
      ],
      onHide: () => {
        if (currentTimeRef.value) {
          const d = {
            h: currentTimeRef.value.hours(),
            m: currentTimeRef.value.minutes(),
            s: currentTimeRef.value.seconds()
          };
          view.dispatch(
            view.state.tr.insertText(
              durationToStr(d),
              pos + 1,
              pos + prevText.length + 1
            )
          );
        }
      }
    });
  };

  const handleCountdown = () => {
    showPopup({
      size: "mini",
      content: <Timer duration={match} />,
      actions: [
        {
          text: "Close",
          fn: "hide"
        }
      ]
    });
  };
  const handleDouble = () => {
    const m = { ...match };

    Object.keys(m).forEach(k => {
      m[k] = m[k] * 2;
    });

    normalizeDuration(m);

    const text = durationToStr(m);
    view.dispatch(
      view.state.tr.insertText(text, pos + 1, pos + prevText.length + 1)
    );
  };

  return (
    <div className="options">
      <List selection verticalAlign="middle">
        <List.Item onClick={handleCountdown}>
          <List.Content floated="right">CTRL + Enter</List.Content>
          <Icon name="stopwatch" color="olive" />
          <List.Content>
            <List.Header id="duration--countdown">Countdown</List.Header>
          </List.Content>
        </List.Item>

        <List.Item onClick={handleContinue}>
          <List.Content floated="right">CTRL + Shift + Enter</List.Content>
          <Icon name="clock" color="olive" />
          <List.Content>
            <List.Header id="duration--continue">Continue</List.Header>
          </List.Content>
        </List.Item>

        <List.Item onClick={handleDouble}>
          <List.Content floated="right">CTRL + Shift + 8</List.Content>
          <Icon name="angle double up" color="olive" />
          <List.Content>
            <List.Header id="duration--double">Double</List.Header>
          </List.Content>
        </List.Item>
      </List>
    </div>
  );
};
