import React from "react";
import moment from "moment";
import { saveAs } from "file-saver";
import { count } from "@wordpress/wordcount";
import {
  Statistic,
  Header,
  Icon,
  Label,
  Divider,
  Button,
  Grid
} from "semantic-ui-react";

import { getStorage } from "../Storage";
import { formatDate, formatDateTime, getZeroBasedNum } from "../util";

import "./Stats.css";

export default function Stats({ todos, getTextContent }) {
  const today = formatDate(new Date());
  const notDone = todos.filter(item => !item.done);
  const selected = getStorage().getSelectedNote();
  const text = getTextContent();
  const words = count(text, "words", {});
  const readingTime = (d => `${
    d.hours() ? d.hours() + "h" : ""
  }${" "}${getZeroBasedNum(d.minutes())}m
  `)(moment.duration(words / 200, "minutes"));

  window.getTextContent = getTextContent;
  const exportAsText = e => {
    e.preventDefault();
    const blob = new Blob([text], { type: "text/plain;charset=utf-8" });
    saveAs(blob, `${selected.meta.title}.txt`);
  };

  return (
    <div className="Editor--stats">
      <Header
        as="h2"
        className="Stats--title"
        content={`Statistics`}
        subheader={` for ${selected.meta.title}`}
      />
      <Grid columns="equal" textAlign="center">
        <Grid.Column>
          <Header as="h3">
            <Icon name="time" />
            <Header.Content>
              Creation Date
              <Header.Subheader>
                {formatDateTime(selected.created_at)}
              </Header.Subheader>
            </Header.Content>
          </Header>
        </Grid.Column>
        <Grid.Column>
          <Header as="h3">
            <Icon name="time" />
            <Header.Content>
              Modification Date
              <Header.Subheader>
                {formatDateTime(selected.updated_at)}
              </Header.Subheader>
            </Header.Content>
          </Header>
        </Grid.Column>
      </Grid>
      <Divider className="Stats--divider" />
      <Statistic.Group widths={3} className="Stats--group">
        <Statistic>
          <Statistic.Value>{notDone.length}</Statistic.Value>
          <Statistic.Label>items to do</Statistic.Label>
        </Statistic>
        <Statistic>
          <Statistic.Value>
            {
              notDone.filter(item => item.date && item.date.includes(today))
                .length
            }
          </Statistic.Value>
          <Statistic.Label>items to do today</Statistic.Label>
        </Statistic>
        <Statistic className="Stats--stat">
          <Statistic.Value>{todos.length - notDone.length}</Statistic.Value>
          <Statistic.Label>items done</Statistic.Label>
        </Statistic>
      </Statistic.Group>
      <Statistic.Group widths={3}>
        <Statistic className="Stats--stat">
          <Statistic.Value>{text.length}</Statistic.Value>
          <Statistic.Label>Characters</Statistic.Label>
        </Statistic>
        <Statistic className="Stats--stat">
          <Statistic.Value>{words}</Statistic.Value>
          <Statistic.Label>Words</Statistic.Label>
        </Statistic>
        <Statistic className="Stats--stat" size="mini">
          <Statistic.Value>{readingTime}</Statistic.Value>
          <Statistic.Label>Read Time</Statistic.Label>
        </Statistic>
      </Statistic.Group>
      <Grid columns="equal" textAlign="center">
        <Grid.Column>
          <Divider horizontal className="Stats--divider">
            <Header as="h4">
              <Icon name="tags" color="grey" />
              Tags
            </Header>
          </Divider>
          {selected.meta.tags.map((tag, i) => (
            <Label key={tag} as="a" tag>
              {tag}
            </Label>
          ))}
        </Grid.Column>
        <Grid.Column>
          <Divider horizontal className="Stats--divider">
            <Header as="h4">
              <Icon name="download" color="grey" />
              Download
            </Header>
          </Divider>
          <Button.Group>
            <Button onClick={exportAsText}>.TXT</Button>
            <Button.Or />
            <Button disabled>.PDF</Button>
            <Button.Or />
            <Button disabled>.HTML</Button>
          </Button.Group>
        </Grid.Column>
      </Grid>
    </div>
  );
}
