import * as FirebaseStorage from "./StorageFirebase";
import * as InMemoryStorage from "./StorageInMemory";

let instance = null;

export function createStorage(...args) {
  const [firebase] = args;
  const { email } = firebase.auth().currentUser;
  const isUsingProductionStorage = /@prose\.com$/.test(email);

  if (isUsingProductionStorage) {
    console.log("You are using PRODUCTION storage");
  } else {
    console.log("You are using IN MEMORY storage");
  }

  // TODO: Support for premium accounts.
  instance = isUsingProductionStorage
    ? FirebaseStorage.createStorage(...args)
    : InMemoryStorage.createStorage(...args);
}

export function getStorage() {
  if (!instance) {
    throw new Error("Create store before using it");
  }
  return instance;
}
