// =============================================================================
// Todo

const getTodoIndicatorNode = (state, view) => {
  const { $from, to } = state.selection;
  const same = $from.sharedDepth(to);
  if (same == 0) return false;

  const proseNode = $from.before(same);
  let currentNode = view.nodeDOM(proseNode);
  if (!currentNode.querySelector(".todo")) return false;

  while (
    currentNode.querySelector(".todo") &&
    !currentNode.querySelector(".todo-indicator")
  ) {
    currentNode = currentNode.previousElementSibling;
  }

  return currentNode.querySelector(".todo") ? currentNode : false;
};

export function todoReset(state, dispatch, view) {
  const node = getTodoIndicatorNode(state, view);
  if (!node) return false;

  node.querySelector("[data-value=reset]").click();
  return true;
}
export function todoPlay(state, dispatch, view) {
  const node = getTodoIndicatorNode(state, view);
  if (!node) return false;

  node.querySelector("[data-value=play]").click();
  return true;
}
export function todoClearCompleted(state, dispatch, view) {
  const node = getTodoIndicatorNode(state, view);
  if (!node) return false;

  node.querySelector("[data-value=delete_completed]").click();
  return true;
}
export function todoClearUnfinished(state, dispatch, view) {
  const node = getTodoIndicatorNode(state, view);
  if (!node) return false;

  node.querySelector("[data-value=delete_unfinished]").click();
  return true;
}

