// =============================================================================
// Foldable

export function foldedToggle(state, dispatch, view) {
  const { $from, to } = state.selection;
  const same = $from.sharedDepth(to);
  if (same == 0) return false;

  const proseNode = $from.before(same);
  const currentNode = view.nodeDOM(proseNode);

  if (currentNode.classList.contains("folded-opening")) {
    currentNode.innerText = "....";
    return true;
  }

  if (currentNode.querySelector(".toggleFolded")) {
    currentNode.innerHTML = "...";
    return true;
  }

  return false;
}

// =============================================================================
// Lists

export function indentList(state, dispatch, view) {
  const { $from, to } = state.selection;
  const same = $from.sharedDepth(to);
  if (same == 0) return false;

  const proseNode = $from.before(same);
  const currentNode = view.nodeDOM(proseNode);

  const item =
    currentNode.querySelector(".bullet") ||
    currentNode.querySelector(".ordered");
  if (!item) return false;

  item.textContent = toVisibleSpaces(`    ${item.textContent}`);
  return true;
}

export function unindentList(state, dispatch, view) {
  const { $from, to } = state.selection;
  const same = $from.sharedDepth(to);
  if (same == 0) return false;

  const proseNode = $from.before(same);
  const currentNode = view.nodeDOM(proseNode);

  const item =
    currentNode.querySelector(".bullet") ||
    currentNode.querySelector(".ordered");
  if (!item) return false;

  item.textContent = toVisibleSpaces(
    getTextContent(item).replace(/^\s{1,4}/, "")
  );
  return true;
}
