import React, { useState } from "react";
import ReactDOM from "react-dom";
import { Modal, Image, Header, Button } from "semantic-ui-react";

export function ModalComponent({
  title,
  content,
  actions = [],
  onHide,
  rawContent = false,
  ...modalProps
}) {
  const handleClose = () => {
    hidePopup();
    if (onHide) onHide();
  };

  return (
    <Modal onClose={handleClose} open closeIcon {...modalProps}>
      {title && <Modal.Header>{title}</Modal.Header>}
      {rawContent ? content : <Modal.Content>{content}</Modal.Content>}
      {actions.length > 0 && (
        <Modal.Actions>
          {actions.map(({ text, fn, ...actionProps }, i) => (
            <Button
              key={i}
              onClick={fn === "hide" ? handleClose : fn}
              {...actionProps}
            >
              {text}
            </Button>
          ))}
        </Modal.Actions>
      )}
    </Modal>
  );
}

const popup = document.createElement("div");
export function hidePopup() {
  if (popup.parentNode) popup.parentNode.removeChild(popup);
  ReactDOM.unmountComponentAtNode(popup);
  popup.innerHTML = "";
}

export function showPopup(props) {
  if (!popup.parentNode) document.body.appendChild(popup);
  ReactDOM.render(<ModalComponent {...props} />, popup);
}
