import debounce from "lodash.debounce";
import deepEqual from "deep-equal";

import initialNotes from "./initial.js";
import { showError } from "./errors";
import { onVisibilityChange } from "./visibility";

// TODO:
//
// - [ ] Store last update to local storage and restore on next load
//       if failed. Maybe create copy of note like bear does.
// - [ ] Do an alternative localstorage storage.
//

const LOCAL_STORAGE_KEY = "__prose-editor-selected_note__";

function getUserTemplate() {
  return {
    notes: {}
  };
}

function getNoteTemplate(id, userId, db) {
  return {
    id,
    contentRef: db.doc(`${userId}/${id}`),
    meta: {
      title: null,
      tags: []
    },
    created_at: new Date(),
    updated_at: new Date(),
    deleted_at: null
  };
}

export class Storage {
  constructor(firebase, callback) {
    this._userId = firebase.auth().currentUser.uid;
    this._firebase = firebase;

    this._db = firebase.firestore(); // Firebase data
    this._handlers = []; // Storage for update handlers.

    // TODO:
    // this._db.enablePersistence({ synchronizeTabs: true });

    // Selected note is not part of the state as user may have multiple
    // app instances at the same time. It is kept only in local storage.
    this._selectedNote = null;
    this._selectedNoteContent = null;

    // State is the data persisted in Firebase.
    this.state = null; // TODO: Eventually rename to user data.

    (async () => {
      const userDoc = this.getUserDoc();

      console.count("FIREBASE_READ");
      let userData = await userDoc.get();
      if (!userData.exists) {
        console.count("FIREBASE_WRITE");
        await userDoc.set(getUserTemplate());

        console.count("FIREBASE_READ");
        userData = await userDoc.get();
        this.state = userData.data();

        for (let i = 0; i < initialNotes.length; i++) {
          const { content, ...noteData } = initialNotes[i];
          await this.addNewNote(content, noteData);
        }
      } else {
        userData = userData.data();
        this.state = userData;
      }

      if (Object.keys(this.state.notes).length === 0) {
        await this.addNewNote();
      }

      const id = window.localStorage.getItem(LOCAL_STORAGE_KEY);
      if (this.noteExists(id)) {
        await this.selectNote(id, true);
      } else {
        await this.selectNote(Math.max(...Object.keys(this.state.notes)), true);
      }

      userDoc.onSnapshot(async (doc, _) => {
        console.count("FIREBASE_READ");

        const newState = doc.data();
        const isServer = !doc.metadata.hasPendingWrites;
        if (!newState.notes[this._selectedNote]) {
          // NOTE: If selected note is missing, select the last updated
          //       note is there are notes at all.
          const lastNote = Object.values(newState.notes).sort(
            (a, b) => b.updated_at.seconds - a.updated_at.seconds
          )[0];

          if (lastNote) {
            await this.selectNote(lastNote.id);
          }
        } else if (
          isServer &&
          !deepEqual(
            newState.notes[this._selectedNote],
            this.state.notes[this._selectedNote]
          )
        ) {
          // NOTE: If we have a server update for current note re-select
          //       in order to reaload its content.
          await this.selectNote(this._selectedNote, true);
        }

        this.state = newState;
        if (Object.keys(this.state.notes).length === 0) {
          this.addNewNote().then(() => {
            this.handleUpdate();
          });
        } else {
          this.handleUpdate({ isServer });
        }
      });
    })()
      .then(() => {
        callback();
      })
      .catch(error => {
        showError(error);
      });

    // NOTE: Prevent accidental tab close when update is not finished.
    window.addEventListener("beforeunload", e => {
      if (this.hasPendingUpdates()) {
        e.preventDefault();
        e.returnValue = ""; // Chrome requires returnValue to be set
        setTimeout(() => {
          // NOTE: If user ends up allowing the update, do it immediately.
          this._updateNoteDebounced.flush();
        }, 1);
        return false;
      }

      return true;
    });

    // NOTE: Run the pending updates every time user switch to
    //       other tab or application.
    onVisibilityChange(() => {
      if (this.hasPendingUpdates()) {
        this._updateNoteDebounced.flush();
      }
    });
  }

  // ===========================================================================
  // Subscription

  subscribe(f) {
    this._handlers.push(f);
    return () => {
      this._handlers = this._handlers.filter(fn => fn !== f);
    };
  }

  handleUpdate(...args) {
    this._handlers.forEach(h => h(...args));
  }

  // ===========================================================================
  // Pending updates

  hasPendingUpdates = () => this.__pendingUpdates;
  setPendingUpdates = val => (this.__pendingUpdates = val);

  // ===========================================================================
  // Selectors

  getUserDoc() {
    return this._db.collection("users").doc(this._userId);
  }

  getSelectedNote() {
    return this.state.notes[this._selectedNote];
  }

  getSelectedNoteContent() {
    return this._selectedNoteContent;
  }

  noteExists(id) {
    return !!this.state.notes[id];
  }

  getNextNoteId() {
    return Math.max(...Object.keys(this.state.notes), 0) + 1;
  }

  getNotesList() {
    return Object.values(this.state.notes).sort(
      (a, b) => b.updated_at.seconds - a.updated_at.seconds
    );
  }

  getActiveNotes() {
    return this.getNotesList().filter(n => !n.deleted_at);
  }

  getInactiveNotes() {
    return this.getNotesList().filter(n => !!n.deleted_at);
  }

  getNotesByTagsAndDate() {
    const result = {};
    this.getActiveNotes()
      .sort((a, b) => a.updated_at - b.updated_at)
      .forEach(note => {
        note.meta.tags.forEach(tag => {
          if (!result[tag]) result[tag] = [];
          result[tag].push(note);
        });
      });

    return result;
  }

  // ===========================================================================
  // Mutations

  selectNote = async (id, preventUpdate = false) => {
    if (id === this.selectNote) return;

    if (!this.state.notes[id]) {
      throw new Error(`No note with id: ${id}`);
    }

    // NOTE: Make sure calls are in parallel.
    const newContent = await this.state.notes[id].contentRef.get();
    if (this.hasPendingUpdates()) {
      await this._updateNoteDebounced.flush();
    }

    this._selectedNote = id;
    this._selectedNoteContent = (await newContent).data();

    console.count("FIREBASE_READ");
    window.localStorage.setItem(LOCAL_STORAGE_KEY, id);

    if (!preventUpdate) this.handleUpdate();
  };

  addNewNote = (content = {}, noteData = {}) => {
    return (async () => {
      const id = this.getNextNoteId();
      const note = getNoteTemplate(id, this._userId, this._db);

      console.count("FIREBASE_WRITE");
      await this.getUserDoc().update({
        [`notes.${id}`]: { ...note, ...noteData }
      });

      console.count("FIREBASE_WRITE");
      await note.contentRef.set(content);
      this.state.notes[id] = note;

      this.selectNote(id);
    })().catch(error => {
      showError(error);
    });
  };

  _updateNote = (id, data, content) => {
    const { notes } = this.state;

    return (async () => {
      if (content) {
        console.count("FIREBASE_WRITE");
        await notes[id].contentRef.set(content);
      }

      console.count("FIREBASE_WRITE");
      const d = { ...notes[id], ...data, content, contentRef: null };
      console.log(JSON.stringify(d));
      await this.getUserDoc().update({
        [`notes.${id}`]: {
          ...notes[id],
          ...data
        }
      });
    })()
      .then(() => {
        this.handleUpdate();
        this.setPendingUpdates(false);
      })
      .catch(error => {
        showError(error);
      });
  };

  _updateNoteDebounced = debounce(this._updateNote, 5 * 1000, {
    maxWait: 15 * 1000
  });

  updateNote = (id, data, content) => {
    const updateData = {
      ...data,
      updated_at: this._firebase.firestore.Timestamp.now()
    };

    // NOTE: Optimistic instant update. It will be overridden
    // when update notification is received.
    this.state.notes[id] = {
      ...this.state.notes[id],
      ...updateData
    };

    this.setPendingUpdates(true);
    this._updateNoteDebounced(id, data, content);

    // NOTE: Skip content update it is here either ways.
    if (content) this._selectedNoteContent = content;

    this.handleUpdate();
  };

  deleteNote = id => {
    (async () => {
      if (this.hasPendingUpdates()) {
        await this._updateNoteDebounced.flush();
      }
      await this._updateNote(id, {
        deleted_at: this._firebase.firestore.Timestamp.now()
      });
    })().catch(error => {
      showError(error);
    });
  };

  restoreNote = id => {
    (async () => {
      if (this.hasPendingUpdates()) {
        await this._updateNoteDebounced.flush();
      }
      await this._updateNote(id, {
        deleted_at: null
      });
    })().catch(error => {
      showError(error);
    });
  };

  permanentlyDeleteNote = id => {
    (async () => {
      if (this.hasPendingUpdates()) {
        await this._updateNoteDebounced.flush();
      }

      const note = this.state.notes[id];
      await Promise.all([
        note.contentRef.delete(),
        this.getUserDoc().update({
          [`notes.${id}`]: this._firebase.firestore.FieldValue.delete()
        })
      ]);
    })().catch(error => {
      showError(error);
    });
  };

  // ===========================================================================
  // Files

  uploadFile = file => {
    const { _firebase, _userId } = this;

    const now = new Date();
    const name = `Photo ${now.getDate()} ${now.toLocaleString("default", {
      month: "long"
    })} ${now.getFullYear()}, ${Date.now()}.${
      file.name.split(".").slice(-1)[0]
    }`;

    const userDirRef = _firebase.storage().ref(_userId);
    const storageRef = userDirRef.child(name);

    return storageRef.put(file).then(doc => storageRef.getDownloadURL());
  };
}

// =============================================================================
// Exports

let instance = null;

export function createStorage(...args) {
  return (instance = new Storage(...args));
}

export function getStorage() {
  if (!instance) {
    throw new Error("Create store before using it");
  }
  return instance;
}
